import { createClient } from '@supabase/supabase-js'

const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL!
const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!

export const supabase = createClient(supabaseUrl, supabaseAnonKey)

// Types for our database
export interface Product {
  id: string
  name: string
  description: string
  price: number
  category: string
  images: string[]
  variants: {
    sizes: string[]
    colors: string[]
  }
  stock: number
  featured: boolean
  created_at: string
}

export interface Order {
  id: string
  user_id: string
  items: {
    product_id: string
    product_name: string
    quantity: number
    price: number
    size?: string
    color?: string
  }[]
  total: number
  status: 'pending' | 'processing' | 'shipped' | 'delivered' | 'cancelled'
  shipping_address: {
    name: string
    email: string
    address: string
    city: string
    phone: string
    notes?: string
  }
  payment_method?: string
  created_at: string
}

export interface UserProfile {
  id: string
  email: string
  name: string
  phone?: string
  address?: string
  city?: string
  language: 'en' | 'ar'
  role: 'customer' | 'admin'
  created_at: string
}

export interface CartItem {
  product: Product
  quantity: number
  size?: string
  color?: string
}
