'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardFooter } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import Navigation from '@/components/Navigation'
import { supabase, type Product } from '@/lib/supabase'
import { useCart } from '@/contexts/CartContext'
import { getTranslation, type Locale } from '@/lib/i18n'
import { toast } from 'sonner'

export default function HomePage() {
  const [locale, setLocale] = useState<Locale>('en')
  const [featuredProducts, setFeaturedProducts] = useState<Product[]>([])
  const [loading, setLoading] = useState(true)
  const { addToCart } = useCart()
  const t = (key: string) => getTranslation(locale, key as any)

  useEffect(() => {
    fetchFeaturedProducts()
  }, [])

  const fetchFeaturedProducts = async () => {
    try {
      // Check if we're in demo mode
      const isDemoMode = process.env.NEXT_PUBLIC_SUPABASE_URL === 'https://demo.supabase.co'
      
      if (isDemoMode) {
        // Use demo data
        const { demoProducts } = await import('@/lib/demo-data')
        const featuredProducts = demoProducts.filter(product => product.featured)
        setFeaturedProducts(featuredProducts)
        setLoading(false)
        return
      }

      // Use real Supabase data
      const { data, error } = await supabase
        .from('products')
        .select('*')
        .eq('featured', true)
        .limit(6)

      if (error) throw error
      setFeaturedProducts(data || [])
    } catch (error) {
      console.error('Error fetching featured products:', error)
      // Fallback to demo data on error
      const { demoProducts } = await import('@/lib/demo-data')
      const featuredProducts = demoProducts.filter(product => product.featured)
      setFeaturedProducts(featuredProducts)
    } finally {
      setLoading(false)
    }
  }

  const handleAddToCart = (product: Product) => {
    addToCart(product, 1)
    toast.success(t('productAdded'))
  }

  const formatPrice = (price: number) => {
    return new Intl.NumberFormat(locale === 'ar' ? 'ar-SA' : 'en-US', {
      style: 'currency',
      currency: 'USD'
    }).format(price)
  }

  return (
    <div className={`min-h-screen bg-gray-50 ${locale === 'ar' ? 'rtl' : 'ltr'}`}>
      <Navigation locale={locale} onLocaleChange={setLocale} />
      
      {/* Hero Section */}
      <section className="bg-white">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-20">
          <div className="text-center">
            <h1 className="text-4xl md:text-6xl font-bold text-gray-900 mb-6">
              {t('welcomeTitle')}
            </h1>
            <p className="text-xl text-gray-600 mb-8 max-w-2xl mx-auto">
              {t('welcomeSubtitle')}
            </p>
            <div className="mb-12">
              <img 
                src="https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/c7543873-e33d-44b8-b97a-86cb700e5790.png" 
                alt="Modern fashion hero banner featuring elegant models showcasing trending styles and contemporary designs"
                className="w-full h-64 md:h-96 object-cover rounded-lg shadow-lg"
                onError={(e) => {
                  e.currentTarget.src = '/fallback-hero.jpg'
                }}
              />
            </div>
            <Link href="/catalog">
              <Button size="lg" className="text-lg px-8 py-3">
                {t('shopNow')}
              </Button>
            </Link>
          </div>
        </div>
      </section>

      {/* Featured Products */}
      <section className="py-16">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-12">
            <h2 className="text-3xl font-bold text-gray-900 mb-4">
              {t('featuredProducts')}
            </h2>
            <p className="text-gray-600 max-w-2xl mx-auto">
              Discover our handpicked selection of the season's most stylish pieces
            </p>
          </div>

          {loading ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
              {[...Array(6)].map((_, i) => (
                <Card key={i} className="animate-pulse">
                  <div className="h-64 bg-gray-200 rounded-t-lg"></div>
                  <CardContent className="p-4">
                    <div className="h-4 bg-gray-200 rounded mb-2"></div>
                    <div className="h-4 bg-gray-200 rounded w-2/3"></div>
                  </CardContent>
                </Card>
              ))}
            </div>
          ) : (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
              {featuredProducts.map((product) => (
                <Card key={product.id} className="group hover:shadow-lg transition-shadow">
                  <div className="relative overflow-hidden rounded-t-lg">
                    <img
                      src={product.images[0] || `https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/8648636b-30b8-42a3-ac9f-b1953f13ef44.png)}`}
                      alt={product.name}
                      className="w-full h-64 object-cover group-hover:scale-105 transition-transform duration-300"
                      onError={(e) => {
                        e.currentTarget.src = `https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/8648636b-30b8-42a3-ac9f-b1953f13ef44.png)}`
                      }}
                    />
                    {product.stock <= 5 && product.stock > 0 && (
                      <Badge className="absolute top-2 left-2 bg-orange-500">
                        Only {product.stock} left
                      </Badge>
                    )}
                    {product.stock === 0 && (
                      <Badge className="absolute top-2 left-2 bg-red-500">
                        {t('outOfStock')}
                      </Badge>
                    )}
                  </div>
                  <CardContent className="p-4">
                    <h3 className="font-semibold text-lg mb-2">{product.name}</h3>
                    <p className="text-gray-600 text-sm mb-3 line-clamp-2">
                      {product.description}
                    </p>
                    <div className="flex items-center justify-between">
                      <span className="text-2xl font-bold text-gray-900">
                        {formatPrice(product.price)}
                      </span>
                      <Badge variant="secondary">{t(product.category)}</Badge>
                    </div>
                  </CardContent>
                  <CardFooter className="p-4 pt-0 flex gap-2">
                    <Link href={`/product/${product.id}`} className="flex-1">
                      <Button variant="outline" className="w-full">
                        View Details
                      </Button>
                    </Link>
                    <Button 
                      onClick={() => handleAddToCart(product)}
                      disabled={product.stock === 0}
                      className="flex-1"
                    >
                      {product.stock === 0 ? t('outOfStock') : t('addToCart')}
                    </Button>
                  </CardFooter>
                </Card>
              ))}
            </div>
          )}

          <div className="text-center mt-12">
            <Link href="/catalog">
              <Button variant="outline" size="lg">
                {t('viewAll')} {t('products')}
              </Button>
            </Link>
          </div>
        </div>
      </section>

      {/* Categories Section */}
      <section className="py-16 bg-white">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-12">
            <h2 className="text-3xl font-bold text-gray-900 mb-4">
              Shop by Category
            </h2>
            <p className="text-gray-600">
              Find exactly what you're looking for
            </p>
          </div>

          <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-6 gap-4">
            {['shirts', 'dresses', 'jeans', 'blouses', 'jackets', 'sweaters'].map((category) => (
              <Link key={category} href={`/catalog?category=${category}`}>
                <Card className="group hover:shadow-md transition-shadow cursor-pointer">
                  <CardContent className="p-6 text-center">
                    <div className="w-16 h-16 mx-auto mb-4 bg-gray-100 rounded-full flex items-center justify-center group-hover:bg-gray-200 transition-colors">
                      <span className="text-2xl">ðŸ‘”</span>
                    </div>
                    <h3 className="font-medium text-gray-900">
                      {t(category)}
                    </h3>
                  </CardContent>
                </Card>
              </Link>
            ))}
          </div>
        </div>
      </section>

      {/* Footer */}
      <footer className="bg-gray-900 text-white py-12">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid grid-cols-1 md:grid-cols-4 gap-8">
            <div>
              <h3 className="text-lg font-semibold mb-4">Fashion Store</h3>
              <p className="text-gray-400">
                Your destination for modern fashion and style.
              </p>
            </div>
            <div>
              <h4 className="font-medium mb-4">Quick Links</h4>
              <ul className="space-y-2 text-gray-400">
                <li><Link href="/" className="hover:text-white transition-colors">{t('home')}</Link></li>
                <li><Link href="/catalog" className="hover:text-white transition-colors">{t('catalog')}</Link></li>
                <li><Link href="/cart" className="hover:text-white transition-colors">{t('cart')}</Link></li>
              </ul>
            </div>
            <div>
              <h4 className="font-medium mb-4">Categories</h4>
              <ul className="space-y-2 text-gray-400">
                <li><Link href="/catalog?category=shirts" className="hover:text-white transition-colors">{t('shirts')}</Link></li>
                <li><Link href="/catalog?category=dresses" className="hover:text-white transition-colors">{t('dresses')}</Link></li>
                <li><Link href="/catalog?category=jeans" className="hover:text-white transition-colors">{t('jeans')}</Link></li>
              </ul>
            </div>
            <div>
              <h4 className="font-medium mb-4">Contact</h4>
              <p className="text-gray-400">
                Email: info@fashionstore.com<br />
                Phone: +1 (555) 123-4567
              </p>
            </div>
          </div>
          <div className="border-t border-gray-800 mt-8 pt-8 text-center text-gray-400">
            <p>&copy; 2024 Fashion Store. All rights reserved.</p>
          </div>
        </div>
      </footer>
    </div>
  )
}
