'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { useRouter } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { useCart } from '@/contexts/CartContext'
import { supabase } from '@/lib/supabase'
import { getTranslation, type Locale } from '@/lib/i18n'

interface NavigationProps {
  locale: Locale
  onLocaleChange: (locale: Locale) => void
}

export default function Navigation({ locale, onLocaleChange }: NavigationProps) {
  const [user, setUser] = useState<any>(null)
  const [userProfile, setUserProfile] = useState<any>(null)
  const { getItemCount } = useCart()
  const router = useRouter()
  const t = (key: string) => getTranslation(locale, key as any)

  useEffect(() => {
    // Check if we're in demo mode
    const isDemoMode = process.env.NEXT_PUBLIC_SUPABASE_URL === 'https://demo.supabase.co'
    
    if (isDemoMode) {
      // Demo mode - check localStorage
      const demoUser = localStorage.getItem('demo-user')
      if (demoUser) {
        const parsedUser = JSON.parse(demoUser)
        setUser({ email: parsedUser.email, id: parsedUser.id })
        setUserProfile(parsedUser)
      }
      return
    }

    // Real Supabase authentication
    supabase.auth.getSession().then(({ data: { session } }) => {
      setUser(session?.user ?? null)
      if (session?.user) {
        fetchUserProfile(session.user.id)
      }
    })

    // Listen for auth changes
    const { data: { subscription } } = supabase.auth.onAuthStateChange((_event, session) => {
      setUser(session?.user ?? null)
      if (session?.user) {
        fetchUserProfile(session.user.id)
      } else {
        setUserProfile(null)
      }
    })

    return () => subscription.unsubscribe()
  }, [])

  const fetchUserProfile = async (userId: string) => {
    const { data, error } = await supabase
      .from('user_profiles')
      .select('*')
      .eq('id', userId)
      .single()
    
    if (data) {
      setUserProfile(data)
    }
  }

  const handleLogout = async () => {
    const isDemoMode = process.env.NEXT_PUBLIC_SUPABASE_URL === 'https://demo.supabase.co'
    
    if (isDemoMode) {
      // Demo mode - clear localStorage
      localStorage.removeItem('demo-user')
      setUser(null)
      setUserProfile(null)
      router.push('/')
      return
    }

    // Real Supabase logout
    await supabase.auth.signOut()
    router.push('/')
  }

  const toggleLanguage = () => {
    const newLocale = locale === 'en' ? 'ar' : 'en'
    onLocaleChange(newLocale)
  }

  return (
    <nav className={`bg-white shadow-sm border-b ${locale === 'ar' ? 'rtl' : 'ltr'}`}>
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-16">
          {/* Logo */}
          <Link href="/" className="text-2xl font-bold text-gray-900">
            Fashion Store
          </Link>

          {/* Main Navigation */}
          <div className="hidden md:flex items-center space-x-8">
            <Link href="/" className="text-gray-700 hover:text-gray-900 transition-colors">
              {t('home')}
            </Link>
            <Link href="/catalog" className="text-gray-700 hover:text-gray-900 transition-colors">
              {t('catalog')}
            </Link>
            {user && (
              <Link href="/dashboard" className="text-gray-700 hover:text-gray-900 transition-colors">
                {t('dashboard')}
              </Link>
            )}
            {userProfile?.role === 'admin' && (
              <Link href="/admin" className="text-gray-700 hover:text-gray-900 transition-colors">
                {t('admin')}
              </Link>
            )}
          </div>

          {/* Right side actions */}
          <div className="flex items-center space-x-4">
            {/* Language Toggle */}
            <Button
              variant="outline"
              size="sm"
              onClick={toggleLanguage}
              className="text-sm"
            >
              {locale === 'en' ? 'Ø§Ù„Ø¹Ø±Ø¨ÙŠØ©' : 'English'}
            </Button>

            {/* Cart */}
            <Link href="/cart">
              <Button variant="outline" size="sm" className="relative">
                {t('cart')}
                {getItemCount() > 0 && (
                  <Badge className="absolute -top-2 -right-2 h-5 w-5 rounded-full p-0 flex items-center justify-center text-xs">
                    {getItemCount()}
                  </Badge>
                )}
              </Button>
            </Link>

            {/* Auth buttons */}
            {user ? (
              <div className="flex items-center space-x-2">
                <span className="text-sm text-gray-700">
                  {userProfile?.name || user.email}
                </span>
                <Button variant="outline" size="sm" onClick={handleLogout}>
                  {t('logout')}
                </Button>
              </div>
            ) : (
              <div className="flex items-center space-x-2">
                <Link href="/auth/login">
                  <Button variant="outline" size="sm">
                    {t('login')}
                  </Button>
                </Link>
                <Link href="/auth/register">
                  <Button size="sm">
                    {t('register')}
                  </Button>
                </Link>
              </div>
            )}
          </div>
        </div>

        {/* Mobile menu */}
        <div className="md:hidden pb-4">
          <div className="flex flex-col space-y-2">
            <Link href="/" className="text-gray-700 hover:text-gray-900 py-2">
              {t('home')}
            </Link>
            <Link href="/catalog" className="text-gray-700 hover:text-gray-900 py-2">
              {t('catalog')}
            </Link>
            {user && (
              <Link href="/dashboard" className="text-gray-700 hover:text-gray-900 py-2">
                {t('dashboard')}
              </Link>
            )}
            {userProfile?.role === 'admin' && (
              <Link href="/admin" className="text-gray-700 hover:text-gray-900 py-2">
                {t('admin')}
              </Link>
            )}
          </div>
        </div>
      </div>
    </nav>
  )
}
